<template>
  <div id="app">
    <section v-show="internalErrorCode < 50">
      <notificationTab
        :notification="notification.text"
        :type="notification.type"
        :active="notification.active"
      ></notificationTab>
      <div v-show="isPageloaded" class="screencontainer fade-in">
        <section v-show="currentPage < 2" id="center_box">
            <!--LoginScreen-->
            <div v-show="currentPage === 0">
              <loginScreen @changepage="currentPage = $event"></loginScreen>
            </div>
            <!--Register-->
            <div v-show="currentPage === 1">
              <registerScreen @changepage="currentPage = $event"></registerScreen>
            </div>
        </section>
        
        <transparencyButton
          @buttonClick="emmiter('closegame')"
        ></transparencyButton>
        <!--WRITTEN TEST-->
        <div v-show="currentPage === 2">
          <quizScreen @changePage="currentPage = $event" @showNotification="showErrorNotf"></quizScreen>
        </div>
        <!--CHAR CREATION-->
        <div v-show="currentPage === 3">
          <charScreen
          
          ></charScreen>
        </div>
        
      </div>

      <!--misc-->
    
      <credits></credits>
    </section>
    <!--WORK IN PROGRESS-->
    <section v-show="internalErrorCode > 50">
      <div v-show="internalErrorCode === 500">
        <h1>INTERNAL ERROR</h1>
        <h1> 500 </h1>
      </div>
      <div v-show="internalErrorCode === 700">
        <h1>Tu foste banido por um administrador</h1>
        <h1>BAN#{{banInformation.banID}}</h1>
        <h1>Razão: {{banInformation.reason}}</h1>
      </div>
    </section> 
  </div>
  
</template>

<script>
//
//Azul #3155df
//Verde #32b85f
//preto #1a1d24
//branco #f6f6f6
//cinzento #dadada
//cinzento escuro #2e313a
//vermelho #8c2d2d
import loginScreen from './pages/loginScreen.vue'
import registerScreen from './pages/registerScreen.vue'
import credits from './components/auth/credits.vue'
import transparencyButton from './components/transparencyButton.vue'
import quizScreen from './pages/quizScreen.vue'
import charScreen from './pages/charScreen.vue'
import notificationTab from './components/notificationTab.vue'

export default {
  name: 'App',
  components: {
    loginScreen,
    registerScreen,
    credits,
    transparencyButton,
    quizScreen,
    charScreen,
    notificationTab
    
  },
  data() {
    return {
      currentPage: 0,
      isPageloaded: false, //DEFAULT: FALSE


      notification: {
        isRunning: false,
        type: null,
        text: '',
        active: true,
      },

      banInformation: {
        banID: 0,
        reason: ''
      },

      internalErrorCode: 0
    }
  },
  methods: {
    //SETS THE PROPER PAGE DEPENDING EITHER THE USER IS REGISTED OR NOT
    initAuth: function(isAlreadyRegisted){
      this.isPageloaded = true
      if(isAlreadyRegisted) {
        this.currentPage = 0;
      } else {
        this.currentPage = 1;
      }
    },

    emmiter: function (eventName, ...args) {
        alt.emit(eventName, ...args);
    },

    //Error and redirects the player to the login screen
    showErrorNotf: function (...args) {
      this.showNotification(args[0], args[1], 3) 
      setTimeout(() => {
        this.currentPage = 0
      }, 3 * 1000);
    },

    showNotification: function (type, text, time) {
      if(!this.notification.isRunning) {
        this.notification.active = true
        this.notification.type = type,
        this.notification.text = text

        setTimeout(() => {
          this.notification.active = false
          this.notification.type = null
          this.notification.text = ''
        }, time * 1000);
      }
    },

    changeToChar: function () {
      this.currentPage = 3
    }, 
    
    PageError: function (err) {
      if(err === 500) {
        this.isPageloaded = true
        this.internalErrorCode = 500
      }
    },

    PlayerBanned: function (banInformation) {
      this.internalErrorCode = 700
      this.banInformation.banID = banInformation.banID
      this.banInformation.reason = banInformation.reason

    },
  },
  mounted() {
    if('alt' in window) {
      setTimeout(() => {
        alt.emit('auth/ready')
      }, 10);
      //event listeners
      alt.on('auth/status', this.initAuth)
      alt.on('auth/createChar', this.changeToChar)
      alt.on('auth/PageError', this.PageError)
      alt.on('auth/showBan', this.PlayerBanned)
      
    }
  },
}
</script>

<style>
/*FONTS*/
@import url('./assets/fonts/fontFamily.css');

#center_box {
    position: absolute;
    background-color: transparent;
    border: 1px solid transparent;
    border-radius: 5px;
    top: 15%;
    left: 30%;
    width: 40%;
    padding: 2% 0% 4% 0%;
    height: auto;
}
/*MAIN BODY LIMITER */
body, html {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #2e313a36;
}

#app {
  font-family: OpenSans-Light;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #f6f6f6;
  user-select: none;
}

.fade-in {
	opacity: 1;
	animation-name: fadeInOpacity;
	animation-iteration-count: 1;
	animation-timing-function: ease-in;
	animation-duration: .4s;
}

@keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}


.fade-enter-active, .fade-leave-active {
  transition: fadeInOpacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
  opacity: 0
}


</style>
