<template>
  <div>
      
    <div id="button_container" @click="button_pressed" :disabled="loading">
        <p class="button_label" v-bind:class="{labelOpacity : loading}">{{label}}</p>

            <div v-show="loading" class="fade-in">
                <spinner class="spinner_mod"></spinner>
            </div>
    </div>
    
  </div>
  
</template>

<script>
import spinner from '../components/spinnerAnim.vue'
export default {
components: {
    spinner
},
props: {
    loading: Boolean,
    label: String,
    color: String,
    border_color: String,
    icon: String,
},
data() {
    return {
  
    }
},
methods: {
    button_pressed: function(e) {
        this.$emit('buttonClick', e)
    },

    
},
mounted() {
  
},
}
</script>

<style scope>

.image_container {
  position: absolute;
  padding: 0vw;
  width: 1.6vw;
  left: 6vw;
  top: .1vw;
  height: 100%;
  filter: contrast(1)
}

.spinner_mod {
    position: absolute;
    margin: 0%;
    padding: 0%;
    top: 10%;
    width: 100%;
    height: 100%;
    animation: 1s spinner;
}

@keyframes spinner {
    from{opacity: 0;}
    to{opacity: 1;}
}

.labelOpacity {
    animation: 1s labelDisappear forwards;
}

@keyframes labelDisappear {
    from{opacity: 1;}
    to{opacity: 0;}
}

#button_container {
    position: relative;
    margin-top: 4%;
    background-color: #3155df;
    border: 2px solid #283775;
    left: 25%;
    width: 50%;
    border-radius: 10px;
    cursor: pointer;
    transition: .2s;
}

#button_container:hover {
    background-color: #152f99;
    border: 2px solid #181818;
}

.button_label {
    font-family: OpenSans-SemiBold;
    font-size: 1vw;
}

</style>