<template>
<div>
  <div>
    <div class="box_position">
      <div v-show="cloudState" class="help_cloud fade-in" >
        <h3 class="help_cloud_text" v-html="info"></h3>
      </div> 
    </div>
    <div class="inputContainer" v-bind:class="{error_input : error_text.length > 1}">
      <img class="image_container" v-bind:class="{svg_error : error_text.length > 1}" :src='"../assets/icons/" + icon + ".svg"'>
      <hr class="img_separator" v-bind:class="{hr_error : error_text !== undefined}">
      <input class="text_container" :type="type" :placeholder="placeholder" v-model="input_text" :disabled="isDisabled">
      <img v-show="isCapsLockOn" class="capslock_container" src="../assets/icons/capslock.svg">
      <img v-show="info !== undefined" class="info_container" src="../assets/icons/info.svg" @mouseover="checkinfo" @mouseleave="cloudState = false">
    </div>
    <div class="error_container fade-in">
      <h3 class="error_label">{{error_text}}</h3>
    </div>
  </div>
</div>
</template>

<script>
export default {
  props: {
    type: String,
    placeholder: String,
    icon: String,
    info: String,
    error_msg: String,
    isDisabled: Boolean,
    showCaps: Boolean,
    setText: String,
    setError: String,
  },
  data() {
    return {
      cloudState: false,
      isCapsLockOn: false,
      input_text: '',
      error_text: '',
    }
  },
  methods: {
    checkinfo: function () {
      this.cloudState = true
    },

    //CHECK THE CAPS
    checkcaps: function(e) {
      var caps_lock_on = e.getModifierState('CapsLock');

      if(caps_lock_on == true) { 
        this.isCapsLockOn = true
      } else {
        this.isCapsLockOn = false
      }
    }
  },
  mounted() {
    //CHECK CAPSLOCK STATUS
    if(this.showCaps) {
      document.querySelector('body').addEventListener('keyup', this.checkcaps)
    }
  },
  watch: {
    input_text: function (val, newval) {
      this.$emit('getText', val)
    },

    setText: function (val, newval) {
      this.input_text = val
    },

    setError: function (val, newval) {
      this.error_text = val
    }
  },

}
</script>

<style scoped>

.box_position {
  position: relative;
  height: 100%;
  width: 100%;
  top: 50%;
  left: 0%;
}

.error_container {
  position: absolute;
  height: auto;
  width: 50%;
  left: 25%;
  height: 5%;
  background-color: #8c2d2d00;
}
.error_label {
  font-family: OpenSans-LightItalic;
  position: relative;
  margin: 0;
  padding: 0;
  margin-top: 1%;
  font-size: .7vw;
  text-align: left;
  left: 0%;
  color: red;
  animation: 1s error_label;
  opacity: 1;
}

@keyframes error_label  {
  from{opacity: 0;}
  to{opacity: 1;}
}

.fade-enter-active, .fade-leave-active {
  transition: opacity 1s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.help_cloud_text {
  position: relative;
  text-align: left;
  margin: 0%;
  padding: 0%;
  font-size: .8vw;
}

.help_cloud {
  position: absolute;
  padding: 2%;
  margin: 0;
  width: 15vw;
  height: auto;
  top: 0%;
  left: 80%;
  border-radius: 5px 20px 20px 5px;
  background-color: #2e313a;
  border-left: .3vw solid #3155df;
}

.error_input {
  position: relative;
  animation: 1s errorDetected forwards;
}

@keyframes errorDetected {
  from{box-shadow: 1px 1px 4px transparent;border: 1px solid transparent;}
  to{box-shadow: 3px 3px 7px #8c2d2d;border: 1px solid #8c2d2d;}
}

.svg_error {
  animation: 1s svg_error forwards;
}

@keyframes svg_error {
  from {filter: brightness(0%) contrast(0)}
  to{filter: brightness(100%)}
}

.hr_error {
  border: 1px solid red;
}

.capslock_container {
  position: absolute;
  padding: 0vw;
  width: 1.2vw;
  right: 3vw;
  top: 0vw;
  height: 100%;
  transition: .4s;
}

.inputContainer {
    position: relative;
    padding: 0;
    margin: 0;
    left: 25%;
    width: 50%;
    height: auto;
    border-radius: 10px;
    background-color: #2e313a;
    overflow: hidden;
    margin-top: 4%;
}

.img_separator {
  position: absolute;
  opacity: 1;
  outline: 0;
  border-radius: 50px;
  top: .9vw;
  left: 11%;
  border: 1px solid rgba(75, 75, 75, 0.486);
  width: 7%;
  height: .5%;
  transform: rotateZ(90deg);
}

.image_container {
  position: absolute;
  padding: 0vw;
  width: 1.2vw;
  left: .9vw;
  top: 0vw;
  height: 100%;
  filter:contrast(0);
}

.info_container {
   position: absolute;
  padding: 0vw;
  width: 1.2vw;
  right: 1vw;
  top: 0vw;
  height: 100%;
  transition: .4s;
  filter: invert(.6);
}

.info_container:hover {
  filter: invert(0);
}

.text_container {
  color: #f6f6f6;
  font-size: 1vw;
  width: 75%;
  padding: 5% 5% 5% 5%;
  float: right;
  right: 0%;
  outline: 0;
  border: 0;
  background-color: transparent;
}

</style>