<template>
  <div>
    <div class="inputContainer">
        <p class="LabelInputContainer">Data de nascimento</p>
        <input type="date" v-model="dob" id="dateofbirth" :min="getMaxDate(80)" :max="getMaxDate(18)" required>
        <div class="error_container fade-in">
        <h3 class="error_label">{{error}}</h3>
        </div>
    </div>  
</div>
</template>

<script>
export default {
    props: {
        error: String
    },
    data() {
        return {
            dob: ''
        }
    },
    methods: {
        getMaxDate: function(desicrement) {
            var year = new Date().getFullYear();
            var month = new Date().getMonth() + 1;
            var day = new Date().getDate();
            year = year - desicrement
            month < 10 ? month = "0" + month : month;
            day < 10 ? day = "0" + day : day;
            return year + "-"+month+"-"+day
        }
    },
    watch: {
        'dob': function(val) {
            this.$emit('outputDOB', val)
        }
    },
}
</script>

<style scoped>
[type="date"] {
  background:rgba(255, 255, 255, 0) url(https://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/calendar_2.png)  97% 50% no-repeat ;
}
[type="date"]::-webkit-inner-spin-button {
  display: none;
}
[type="date"]::-webkit-calendar-picker-indicator {
  opacity: 0;
}

.DOBContainer {
    margin: 5%;
}
/* custom styles */
body {
  padding: 8%;
  background: #00000010;
  font: 13px/1.4 Geneva, 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Verdana, sans-serif;
}
label {
  display: block;
}
input {
  border: 1px solid #161616d8;
  border-radius: 5px;
  padding: .6vw;
  color: white;
  background-color: rgba(10, 10, 10, 0.356);
  filter: drop-shadow(1px 1px 3px #000000);
  box-shadow: inset 0 3px 6px rgba(0,0,0,0.1);
  height: 100%;
  width: 45%;
}
</style>