<template>
    <div>
        <section v-show="mainPage === 0" class="fade-in">
            <div>
                <bodySelector
                    :showClickers="isClickerShown"
                    @buttonClick="buttonClicked"
                
                ></bodySelector>
            </div>
            <!--CHAR INFORMATION-->
            <div v-show="isClickerShown" class="right_chartab fade-in">
                <div class="page_container small">
                    <charInformation
                    @createChar="createChar"
                    ></charInformation>
                </div>
            </div>
            <!--MODIFIER TAB-->
            <div v-show="!isClickerShown" class="right_chartab fade-in">
                <!--FACIAL-->
                <div class="page_container big" v-show="partPage === 0">
                    <facial></facial>
                </div>
                <!--TORSO-->
                <div class="components_slider" v-show="partPage === 1">
                
                    <slider
                    :label="'Casaco'"
                    :max="Number(drawables.torso)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.torso.draw = $event"
                    ></slider>
         
                
                    <slider
                    :label="'Cor do casaco'"
                    :max="Number(availableTextures.torso)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.torso.texture = $event"
                    ></slider>
                </div>
                <!--UNDERSHIRT-->
                <div class="components_slider" v-show="partPage === 2">
                
                    <slider
                    :label="'Roupa interior'"
                    :max="Number(drawables.undershirt)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.undershirt.draw = $event"
                    ></slider>
         
                
                    <slider
                    :label="'Cor da roupa interior'"
                    :max="Number(availableTextures.undershirt)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.undershirt.texture = $event"
                    ></slider>
                </div>

                 <!--PANTS-->
                <div class="components_slider" v-show="partPage === 3">
                
                    <slider
                    :label="'Calças'"
                    :max="Number(drawables.legs)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.legs.draw = $event"
                    ></slider>
         
                
                    <slider
                    :label="'Cor das Calças'"
                    :max="Number(availableTextures.legs)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.legs.texture = $event"
                    ></slider>
                </div>
                <!--SHOES-->
                <div class="components_slider" v-show="partPage === 4">
                
                    <slider
                    :label="'Sapatos'"
                    :max="Number(drawables.shoes)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.shoes.draw = $event"
                    ></slider>
         
                
                    <slider
                    :label="'Cor dos Sapatos'"
                    :max="Number(availableTextures.shoes)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.shoes.texture = $event"
                    ></slider>
                </div>
                <!--ACESSORIOS-->
                <div class="components_slider" v-show="partPage === 5">
                
                    <slider
                    :label="'Acessorios'"
                    :max="Number(drawables.accessories)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.accessories.draw = $event"
                    ></slider>
         
                
                    <slider
                    :label="'Cor dos acessorios'"
                    :max="Number(availableTextures.accessories)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.accessories.texture = $event"
                    ></slider>
                </div>
                <!--GLOVES-->
                <div class="components_slider" v-show="partPage === 6">
                
                    <slider
                    :label="'Braços'"
                    :max="Number(drawables.arms)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.arms.draw = $event"
                    ></slider>
         
                
                    <slider
                    :label="'Textura'"
                    :max="Number(availableTextures.arms)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.arms.texture = $event"
                    ></slider>
                </div>

                <!--BRACELET-->
                <div class="components_slider" v-show="partPage === 7">
                
                    <slider
                    :label="'Acessorio de pulso esquerdo'"
                    :max="Number(drawables.l_bracelet)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.l_bracelet.draw = $event"
                    ></slider>
         
                
                    <slider
                    :label="'Cor do acessorio'"
                    :max="Number(availableTextures.l_bracelet)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.l_bracelet.texture = $event"
                    ></slider>
                </div>

                <div class="components_slider" v-show="partPage === 8">
                
                    <slider
                    :label="'Acessorio de pulso direito'"
                    :max="Number(drawables.r_bracelet)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.r_bracelet.draw = $event"
                    ></slider>
         
                
                    <slider
                    :label="'Cor do acessorio'"
                    :max="Number(availableTextures.r_bracelet)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.r_bracelet.texture = $event"
                    ></slider>
                </div>

                <!--EAR-->
                <div class="components_slider" v-show="partPage === 9">
                
                    <slider
                    :label="'Acessorio de orelha'"
                    :max="Number(drawables.ears)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.ears.draw = $event"
                    ></slider>
         
                
                    <slider
                    :label="'Cor do acessorio'"
                    :max="Number(availableTextures.ears)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.ears.texture = $event"
                    ></slider>
                </div>

                <!--Hat-->
                <div class="components_slider" v-show="partPage === 10">
                
                    <slider
                    :label="'Chapeu'"
                    :max="Number(drawables.hats)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.hats.draw = $event"
                    ></slider>
         
                
                    <slider
                    :label="'Cor do chapeu'"
                    :max="Number(availableTextures.hats)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.hats.texture = $event"
                    ></slider>
                </div>

                <!--Glasses-->
                <div class="components_slider" v-show="partPage === 11">
                
                    <slider
                    :label="'Oculos'"
                    :max="Number(drawables.glasses)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.glasses.draw = $event"
                    ></slider>
         
                
                    <slider
                    :label="'Cor dos oculos'"
                    :max="Number(availableTextures.glasses)"
                    :min="0"
                    :step="1"
                    :defaultValue="0"
                    @outputValue="data.glasses.texture = $event"
                    ></slider>
                </div>
            </div>


            
            
        </section>
    </div> 
</template>

<script>
//Masks 1 x
//HairStyle 2 -
//Torso 3 x
//Legs 4 --
//Bags and parachutes 5 --
//Shoes 6 --
//Accessories 7 --
//Undershirts 8 --
//BODY ARMOUR 9
//Decal 10 --
//Tops 11 --

//PROPS
//Hats 0
//Glasses 1
//Ears 2
//Watches 3
//Bracelets 4
import bodySelector from '../components/char/bodySelector.vue'
import slider from '../components/char/char_components/slider.vue'
//PAGES ---
import charInformation from '../components/char/char_pages/charInformation.vue'
import facial from '../components/char/char_pages/facial.vue'
export default {
components: {
    bodySelector,
    slider,
    //PAGES ---
    charInformation,
    facial
},
data() {
    return {
        mainPage: 0,
        currentSex: 'm',
        partPage: 0,
        isClickerShown: true,
        checkCharInfo: false,

        drawables: {
            //COMPONENTS
            arms: 0,
            legs: 0,
            shoes: 0,
            accessories: 0,
            undershirt: 0,
            torso: 0,

            //PROPS
            hats: 0,
            glasses:0,
            ears: 0,
            l_bracelet: 0,
            r_bracelet: 0,
        },
        availableTextures: {
            //COMPONENTS
            arms: 0,
            legs: 0,
            shoes: 0,
            accessories: 0,
            undershirt: 0,
            torso: 0,

            //PROPS
            hats: 0,
            glasses:0,
            ears: 0,
            l_bracelet: 0,
            r_bracelet: 0,
        },
        data: {
            //COMPONENTS
            arms: {
                draw: 0,
                texture: 0,
            },
            
            legs: {
                draw: 0,
                texture: 0,
            },
            shoes: {
                draw: 0,
                texture: 0,
            },
            accessories: {
                draw: 0,
                texture: 0,
            },
            undershirt: {
                draw: 0,
                texture: 0,
            },
            torso: {
                draw: 0,
                texture: 0,
            },

            //PROPS
            hats: {
                draw: 0,
                texture: 0,
            },
            glasses: {
                draw: 0,
                texture: 0,
            },
            ears: {
                draw: 0,
                texture: 0,
            },
            l_bracelet: {
                draw: 0,
                texture: 0,
            },
            r_bracelet: {
                draw: 0,
                texture: 0,
            },

        }
        
    }
},
watch: {
    //COMPONENTS
    //1 mask
    //3 arms  :
    //4 legs :
    //5 bags
    //6 shoes :
    //7 accessories :
    //8 undershirt :
    //9 armour
    //10 decals
    //11 torso :

    //PROPS
    //0 hats :
    //1 glasses :
    //2 ears :
    //6 l_bracelet  watches
    //7 r_watches
    
    //COMPONENTS
    'data.arms': {
        handler() {
            alt.emit('auth/creation/getDrawableTextures', 3, this.data.arms.draw)
            alt.emit('auth/creation/clothes', 3, this.data.arms.draw, this.data.arms.texture)
        },
        deep: true
    },
    'data.legs': {
        handler() {
            alt.emit('auth/creation/getDrawableTextures', 4, this.data.legs.draw)
            alt.emit('auth/creation/clothes', 4, this.data.legs.draw, this.data.legs.texture)
        },
        deep: true
    },
    'data.shoes': {
        handler() {
            alt.emit('auth/creation/getDrawableTextures', 6, this.data.shoes.draw)
            alt.emit('auth/creation/clothes', 6, this.data.shoes.draw, this.data.shoes.texture)
        },
        deep: true
    },
    'data.accessories': {
        handler() {
            alt.emit('auth/creation/getDrawableTextures', 7, this.data.accessories.draw)
            alt.emit('auth/creation/clothes', 7, this.data.accessories.draw, this.data.accessories.texture)
        },
        deep: true
    }, 
    'data.undershirt': {
        handler() {
            alt.emit('auth/creation/getDrawableTextures', 8, this.data.undershirt.draw)
            alt.emit('auth/creation/clothes', 8, this.data.undershirt.draw, this.data.undershirt.texture)
        },
        deep: true
    },
    'data.torso': {
        handler() {
            alt.emit('auth/creation/getDrawableTextures', 11, this.data.torso.draw)
            alt.emit('auth/creation/clothes', 11, this.data.torso.draw, this.data.torso.texture)
        },
        deep: true
    }, 
    //PROPS
    'data.hats': {
        handler() {
            alt.emit('auth/creation/getPropsTextures', 0, this.data.hats.draw)
            alt.emit('auth/creation/props', 0, this.data.hats.draw, this.data.hats.texture)
        },
        deep: true
    },
    'data.glasses': {
        handler() {
            alt.emit('auth/creation/getPropsTextures', 1, this.data.glasses.draw)
            alt.emit('auth/creation/props', 1, this.data.glasses.draw, this.data.glasses.texture)
        },
        deep: true
    },
    'data.ears': {
        handler() {
            alt.emit('auth/creation/getPropsTextures', 2, this.data.ears.draw)
            alt.emit('auth/creation/props', 2, this.data.ears.draw, this.data.ears.texture)
        },
        deep: true
    },
    'data.l_bracelet': {
        handler() {
            alt.emit('auth/creation/getPropsTextures', 6, this.data.l_bracelet.draw)
            alt.emit('auth/creation/props', 6, this.data.l_bracelet.draw, this.data.l_bracelet.texture)
        },
        deep: true
    },
    'data.r_bracelet': {
        handler() {
            alt.emit('auth/creation/getPropsTextures', 7, this.data.r_bracelet.draw)
            alt.emit('auth/creation/props', 7, this.data.r_bracelet.draw, this.data.r_bracelet.texture)
        },
        deep: true
    },
},
methods: {

    buttonClicked: function(index) {
        //sex change     
        //COMPONENTS
    //1 mask
    //3 arms  :
    //4 legs :
    //5 bags
    //6 shoes :
    //7 accessories :
    //8 undershirt :
    //9 armour
    //10 decals
    //11 torso :

    //PROPS
    //0 hats :
    //1 glasses :
    //2 ears :
    //6 l_bracelet  watches
    //7 r_watches
        switch (index) {

            //COMPONENTS

            case 1: alt.emit('auth/creation/getDrawableTextures', 11, this.data.torso.draw); break;
            case 2: alt.emit('auth/creation/getDrawableTextures', 8, this.data.undershirt.draw); break;
            case 3: alt.emit('auth/creation/getDrawableTextures', 4, this.data.legs.draw); break;
            case 4: alt.emit('auth/creation/getDrawableTextures', 6, this.data.shoes.draw); break;
            case 5: alt.emit('auth/creation/getDrawableTextures', 7, this.data.accessories.draw); break;
            case 6: alt.emit('auth/creation/getDrawableTextures', 3, this.data.arms.draw); break;
            
            //PROPS

            case 7: alt.emit('auth/creation/getPropsTextures', 0, this.data.hats.draw); break;
            case 8: alt.emit('auth/creation/getPropsTextures', 1, this.data.glasses.draw); break;
            case 9: alt.emit('auth/creation/getPropsTextures', 2, this.data.ears.draw); break;
            case 10: alt.emit('auth/creation/getPropsTextures', 6, this.data.l_bracelet.draw); break;
            case 11: alt.emit('auth/creation/getPropsTextures', 7, this.data.r_bracelet.draw); break;

        }  
        this.isClickerShown = !this.isClickerShown
        this.partPage = index
        alt.emit('auth/create/isClickerShown', index, this.isClickerShown)
    },

    emmiterSex: function(sex) {
        this.mainPage = 1
        alt.emit('auth/create/changeSex', sex)
    },

    setAvailableClothes: function(clothes) {
        this.drawables = clothes
    },

    setAvailableTextures: function (index, textureNum) {
        switch (index) {
            case 3:this.availableTextures.arms = textureNum - 1 ;break;
            case 4:this.availableTextures.legs = textureNum - 1 ;break
            case 6:this.availableTextures.shoes = textureNum - 1 ;break
            case 7:this.availableTextures.accessories = textureNum - 1 ;break;
            case 8:this.availableTextures.undershirt = textureNum - 1 ;break
            case 11:this.availableTextures.torso = textureNum - 1 ;break
        }
    },

    setAvailablePropTextures: function(index, textureNum) {
        //PROPS

        switch (index) {
            //HATS 0
            case 0:this.availableTextures.hats = textureNum; break;
            //GLASSES 1
            case 1:this.availableTextures.glasses = textureNum; break;
            //EARS 2
            case 2:this.availableTextures.ears = textureNum; break;
            //WATCHES 3 
            case 6:this.availableTextures.l_bracelet = textureNum; break;
            //BRACELETS 4
            case 7:this.availableTextures.r_bracelet = textureNum; break;
        }
    },

    createChar: function (data) {
        alt.emit('auth/creation/createChar', data)  
    }


},
mounted() {
    if('alt' in window) {
        alt.on('auth/creation/getComponentDrawables', this.setAvailableClothes)
        alt.on('auth/creation/getComponentTextures', this.setAvailableTextures)
        alt.on('auth/creation/getPropsTextures', this.setAvailablePropTextures)
    }
},

}
</script>

<style scope>
.right_chartab {
    position: absolute;
    height: 100%;
    width: 50%;
    right: 0%;
    top: 0%;
    background: linear-gradient(90deg, rgba(125,125,125,0) 0%, #1a1d24ca 100%);
    transform: perspective(1000px) translateX(0%) rotateY(-5deg);
}

.page_container {
    position: absolute;
    width: 80%;
    right: 5%;
    top: 13%;
    border: 1px solid #29292963;
    border-radius: 15px;
    margin: 0%;
    padding: 0%;
    overflow: hidden;
}

.components_slider {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.123);
    border: 1px solid rgba(0, 0, 0, 0.246);
    height: auto;
    border-radius: 10px;
    width: 60%;
    bottom: 20%;
    right: 50%;
    padding: 4% 0% 5% 0%;
}

.small {
    width: 60%;
    height: 80%;
}

.big {
   width: 60%;
   height: 70%;
}

</style>