<template>
  <div id="quizContainer fade-in">
    <h1 class="exam_label fade-in">Teste de conhecimentos</h1>
    <section v-show="pageState === 0" class="notificationQuiz fade-in">
      <h1>Teremos de avaliar os seus conhecimentos de roleplay</h1>
      <h3>Para poder jogar no nosso servidor roleplay voçe necessitara de provar que sabe alguns conseitos de roleplay basicos.</h3>
      <h3>Necessitara de tirar uma classificaçao entre 80 a 100 para passar.</h3>
      <button class="quiz_acceptance" @click="pageState = 1">Estou pronto</button>
    </section>
    <section v-show="pageState === 1">
      <div v-for="(question, index) in randomList.slice(question_id, question_id <= quantity_questions_total ? question_id + 1 : question_id = question_id)" :key="question[0]" >
        <div class="left_side fade-in">
          <div class="question_header fade-in">
            <h2 class="header_text fade-in" v-html="question.question"></h2>
            <h3 class="question_count">{{question_id}}/10</h3>
          </div>
          <div class="response_container fade-in">
            <quizButtonChecker v-for="(answer, index) in question.answer" :key="answer"
            :index="index"
            :answer="answer"
            @emitClick="receiveClick(index)"       
            :class="{selected : index === optionSelected, notselected : index !== optionSelected}"
            ></quizButtonChecker>
          </div>
        </div>
        <button 
          class="confirm_answer fade-in" 
          :class="{'success fade-in': optionSelected >= 0, none: optionSelected === null}" 
          :disabled="optionSelected === null"
          @click="confirmResponse(question, optionSelected)"
        ><h3>Confirmar resposta</h3>
        </button>
        <div class="right_side fade-in">
          <img class="img_quiz fade-in" :src="require('@/assets/quiz_images/' + quiz[index].img_path)">
        </div>
      </div>
    </section>
    <!--NOTIFICATION BOX AFTER QUIZ-->
    <section v-show="pageState === 2" class="notificationQuiz fade-in">
      <div v-if="score >= score_acceptance">
        <h1>Parabéns !</h1>
        <h3>Passas-te no teste com pontuação de {{score}}/100</h3>
        <button class="quiz_acceptance" @click="startCHARcreation">Continuar</button>
      </div>
      <div v-if="score < score_acceptance">
        <h1>Chumbas-te !</h1>
        <h3>Não conseguis-te passar no teste de conhecimentos com pontuação de {{score}}/100</h3>
        <button class="quiz_acceptance" @click="changeMainPage(0)">Confirmar</button>
      </div>
    </section>
  </div>
</template>

<script>
import quizButtonChecker from '../components/quiz/quizButtonChecker.vue'
import questionFile from '../components/quiz/questions.js'

export default {
components: {
  quizButtonChecker
},
data() {
  return {
    pageState: 0,
    quiz: null,

    optionSelected: null,
    
    score: 0,
    score_quantity: 10,
    score_acceptance: 80,

    question_id: 0,
    quantity_questions_total: 10,
    
    notificationQuiz: false
    
  }
},
methods: {
  receiveClick: function (index) {
    this.optionSelected = index
  },

  //confirm response and go to the next
  confirmResponse: function (question, index) {

    this.question_id = this.question_id + 1
    this.optionSelected = null
    
    //If the option selected it's equal to the solution it will increment points
    if(question.solution === index) {
      this.score = this.score + this.score_quantity
    } else {
      return;
    }
  },

  change_end: function () {
    this.pageState = 2
  },

  changeMainPage: function (page) {
    this.$emit('changePage', page)
  },

  //CHANGE PAGE TO THE CHAR CREATION
  startCHARcreation: function () {
    alt.emit('auth/verifiedAddValue', 1)
  },

  //Return the call back from the server if the register was done correctly
  testresult: function (result) {
    if(result) {
      alt.emit('auth/beginCharCreation')
    } else {
      //SOME ERROR ON DATABASE OR SERVER
      this.$emit('showNotification', 2, "Ocorreu um erro, tenta mais tarde")
    }
  }
},
mounted() {
  if('alt' in window) {
    alt.on('auth/writtenCompletionStatus', this.testresult)
  }
},
computed: {
  randomList: function(){
    return this.quiz.sort(function(){return 0.5 - Math.random()});
  }
},
created() {
  //INITIALIZE ALL QUESTIONS INTO THE QUIZ OBJECT
  this.quiz = questionFile
},

watch: {
  'question_id': function() {
   
    if(this.question_id === this.quantity_questions_total) {
      this.change_end()
    }
  }
},
}
</script>

<style scope>
#quizContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(26, 26, 26, 0.123);
  z-index: 0;
  overflow: hidden;
}

.question_count {
  position: absolute;
  margin: 0%;
  bottom: 0%;
  right: 0%;
  padding: 2%;
}

.exam_label {
  font-family: Opensans-ExtraBold;
  text-align: center;
  -webkit-text-stroke: 1px #000000;
}

.confirm_answer {
  position: absolute;
  font-size: 100%;
  bottom: 7%;
  width: 30%;
  height: 8%;
  left: 14%;
  padding: 0%;
  z-index: 1;
  outline: 0;
  border: 0;
  border-radius: 10px;
  cursor: pointer;
  color: white;
  transition: 1s;
}

.success {
background-color: #32b85fd5;
}

.none {
background-color: #494949d7;
}

.selected {
  background: linear-gradient(90deg, #3f5983 0%, #3f5894 50%,  rgba(125, 125, 125, 0.048) 90%, rgba(125, 125, 125, 0) 100%);
  filter: saturate(1);
}

.notselected {
  background: linear-gradient(90deg, #2e3136 0%, #32363f 50%, rgba(125, 125, 125, 0.007) 90%, rgba(125,125,125,0) 100%);
  filter:saturate(.3);
  transition: .2s;
}

.notselected:hover {
  filter: saturate(1);
}

.notificationQuiz {
  position: absolute;
  padding: 4%;
  height: auto;
  width: 30%;
  left: 30%;
  top: 20%;
  background-color: #2e3136bc;
  border-radius: 20px;
  filter: drop-shadow(10px 0px  10px #0000009d);
}

.img_quiz {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0%;
  background-color: #2e313a0c;
  
}

.left_side {
  position: absolute;
  width: 60%;
  height: 70%;
  top: 15%;
  left: 0%;
  background-color: rgba(88, 88, 88, 0.151);
  
}


.right_side {
  position: absolute;
  width: 40%;
  left: 60%;
  height: 70%;
  top: 15%;
  background-color: #2e31368c;
  
}

.question_header {
  position: absolute;
  width: 100%;
  height: 30%;
  background-color: #303133;
  border-radius: 2px;
  
  filter: drop-shadow(10px 0px  10px #0000009d);
}

.header_text {
  position: relative;
  margin: 0%;
  padding: 1%;
  font-size: 2vw;
  text-size-adjust: auto;
  text-align: left;
  filter: drop-shadow(10px 0px  10px #0000009d);
}

.response_container{
  position: absolute;
  width: 100%;
  height: 70%;
  top:30%;
  background-color: #1a1d243b;
  filter: drop-shadow(10px 0px  10px #0000009d);
}

.quiz_acceptance {
  font-family: openSans-Bold;
  position: relative;
  font-size: 1.2vw;
  padding: 3%;
  width: 70%;
  border: 0;
  outline: 0;
  border-radius: 10px;  
  background-color: #26ac53;
  transition: .2s;
  
}

.quiz_acceptance:hover {
  background-color: #25f069;
}
</style>