<template>
    <div>  
        <div class="mainContainer">
            <p class="leftLabel">{{label}}</p>
            <p class="rightQuantity">{{defaultVal}}/{{max}}</p>
            <div class="container">
                <img src="../../../assets/icons/char/components/pointer.svg" class="leftDesincrement" @click="defaultVal--">
                <img src="../../../assets/icons/char/components/pointer.svg" class="rightIncrement" @click="defaultVal++">
                <input type="range" :min="min" :max="max" :step="step" v-model="defaultVal" class="slider">
            </div>
        </div>
    </div>
</template>

<script>
export default {
props: {
    label: String,
    min: Number,
    max: Number,
    step: Number,
    defaultValue: Number,
},
mounted() {
    this.defaultVal = this.defaultValue
},
data() {
    return {
        defaultVal: 0.0,
    }
},

watch: {
    'defaultVal': function() {
        if(this.defaultVal > this.max) {
            this.defaultVal = this.max
        } else if(this.defaultVal < this.min) {
            this.defaultVal = this.min
        }
        this.$emit('outputValue', this.defaultVal)
    }
},
}
</script>

<style scoped>
.mainContainer {
    position: relative;
    margin-top: 5%;
}

.rightQuantity {
    font-family: Opensans-Italic;
    color: #d6d6d6;
    position: absolute;
    right: 15%;
    bottom: 65%;
    font-size: .8vw;
}

.leftLabel {
    font-family: Opensans-regular;
    position: absolute;
    left: 15%;
    bottom: 55%;
    font-size: .9vw;
    color: white;
}
.leftDesincrement {
  position: absolute;
  left: 3%;
  margin: 0%;
  padding: 0%;
  bottom: 15%;
  font-size: 2vw;
  width: 1.5vw;
  filter: contrast(.8);
  transition: .2s;
}

.rightIncrement {
  position: absolute;
  transform: rotateY(180deg);
  right: 3%;
  margin: 0%;
  padding: 0%;
  bottom: 15%;
  font-size: 2vw;
  width: 1.5vw;
  filter: contrast(.8) ;
  transition: .2s;
}

.rightIncrement:hover {
    filter: contrast(1) brightness(2);
    width: 1.6vw;
}

.leftDesincrement:hover {
    filter: contrast(1) brightness(2);
    width: 1.6vw;
}

.container {
  position: relative;
  background-color: rgba(63, 63, 63, 0.856);
  padding: 0%;
  margin: 0%;
  border-radius: 50px;
  width: 70%;
  left: 15%;
}

.slidecontainer {
  width: 100%; /* Width of the outside container */
}

/* The slider itself */
.slider {
  -webkit-appearance: none;  /* Override default CSS styles */
  appearance: none;
  padding: 0%;
  margin: 5%;
  width: 75%;
  height: 2px; /* Specified height */
  background: #8f8f8f; /* Grey background */
  outline: none; /* Remove outline */
  opacity: 1; /* Set transparency (for mouse-over effects on hover) */
  -webkit-transition: .2s; /* 0.2 seconds transition on hover */
  transition: opacity .2s;
  
}

/* Mouse-over effects */
.slider:hover {
  opacity: 1; /* Fully shown on mouse-over */
}

/* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */
.slider::-webkit-slider-thumb {
  -webkit-appearance: none; /* Override default look */
  appearance: none;
  width: 15px; /* Set a specific slider handle width */
  height: 15px; /* Slider handle height */
  background:#e0e0e0; /* Blue background */
  border: 1px solid rgba(0, 0, 0, 0.377);
  border-radius: 50%;
  cursor: pointer; /* Cursor on hover */
}
</style>