<template>
  <div>
      <div class="h1-border-col1">
        <h1 class="labelText">Informaçao do personagem</h1>

      </div>
      <div class="infoContent">
        <charInput
            :label="'Primeiro nome'"
            :error="error.firstName"
            @outputName="data.firstName = $event"
        ></charInput>
        <charInput
            :label="'Ultimo nome'"
            :error="error.lastName"
            @outputName="data.lastName = $event"
        ></charInput>

            <img src="../../../assets/images/female.svg" width="60vw" class="genderSelect" :class="{selectedfemale: data.sex === 0, female: data.sex === 1}" @click="selectSex(0)">
            <img src="../../../assets/images/male.svg" width="60vw" class="genderSelect" :class="{selectedmale: data.sex === 1, male: data.sex === 0}" @click="selectSex(1)">

        <dataNascimento
            @outputDOB="data.dob = $event"
            :error="error.dob"
        ></dataNascimento>
        <charAltura
            @bringAlt="data.height = $event"
            :error="error.height"
        ></charAltura> 
        <button class="buttonCreate" @click="checkCharInputs">Criar personagem</button>
      </div>
      
  </div>
  

</template>

<script>
import charInput from './charInput.vue'
import dataNascimento from './dataNascimento.vue'
import charAltura from './charAltura.vue'
export default {
    components: {
        charInput,
        dataNascimento,
        charAltura
    },
    data() {
        return {
            sexSelected: 0,
            data: {
                firstName: '',
                lastName: '',
                sex: 1,
                dob: '',
                height: null, 
            },
            error: {
                firstName: '',
                lastName: '',
                sex: null,
                dob: '',
                height: ''
            },

            maxAge: 80,
            minAge: 18,
            
        }
    },
    methods: {
        selectSex: function(sex) {
         
            if(this.data.sex === sex) {return;}
            if(sex === 1) {
                this.data.sex = 1
                alt.emit('auth/changeSex', 1)

            } else {
                this.data.sex = 0
                alt.emit('auth/changeSex', 0)
            }
        },



        checkCharInputs: function () {
            var format = /[ `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/;
            //Primeiro nome
            const p1 = new Promise((resolve) => {

                if(this.data.firstName.length <= 2 || this.data.firstName.length > 16) {
                    this.error.firstName = 'Tamanho inválido'
                    resolve(false)
                } else {
                  
                    if(!format.test(this.data.firstName)) {
                        resolve(true)
                        this.error.firstName = ''
                    } else {
                        this.error.firstName = 'O teu primeiro nome é inválido !'
                        resolve(false)
                    }
                }
            })

            //Segundo Nome
            const p2 = new Promise((resolve) => {
                if(this.data.lastName.length <= 2 || this.data.lastName.length > 16) {
                    this.error.lastName = 'Tamanho inválido'
                    resolve(false)
                } else {
                    if(!format.test(this.data.lastName))  {
                        resolve(true)
                        this.error.lastName = ''
                    } else {
                        this.error.lastName = 'O teu ultimo nome é inválido !'
                        resolve(false)
                    }
                }
            })

            //date of birth
            const p3 = new Promise((resolve) => {
                const date = new Date().getUTCFullYear();
                const getDOBInput= this.data.dob.slice(0,4)
                if(getDOBInput < date - this.maxAge || getDOBInput > date - this.minAge) {
                    this.error.dob = 'Data de nascimento inválida'
                    resolve(false)
                } else {
                    this.error.dob = ''
                    resolve(true)
                }
            })

            //Altura
            const p4 = new Promise((resolve) => {
                if(this.data.height > 150 && this.data.height < 230) {
                    resolve(true)
                    this.error.height = ''
                } else {
                    this.error.height = 'Tamanho inválido'
                    resolve(false)
                }
            })


            Promise.all([p1,p2,p3,p4]).then((result) => {
                if(result[0] && result[1] && result[2] && result[3]) {
                    this.$emit('createChar', this.data)
                } else {return;}
            })
        }
    },

    watch: {
        'data': function() {
            this.$emit('sendCharData', this.data)
        }
    },
}
</script>

<style scope>

.buttonCreate {
    font-family: OpenSans-BoldItalic;
    position: absolute;
    font-size: 1.0vw;
    height: 12%;
    width: 102%;
    bottom: -1%;
    left: -1%;
    color: white;
    transition: .3s;
    background-color: #229749;
    border: 1px solid black;
    outline: 0;
    
}

.buttonCreate:hover {
    background-color: #22b152;
    font-size: 1.2vw;

}


.labelText {
    font-size: 1.7vw;
}
.h1-border-col1 {
    position: absolute;
    top: 0%;
    height: 15%;
    font-weight: 200;
    font-family: OpenSans-LightItalic;
    background-color: #2e313ac5;
    position: absolute;
    text-align: center;
    bottom: 82%;

    width: 100%;
    
    border-bottom: 3px solid#2e313a;
}

.infoContent {
    position: absolute;
    bottom: 0%;
    height: 85%;
    width: 100%;
    background-color: #1e1e1f42;
    transition: .2s;
}

.infoContent::-webkit-scrollbar {
    background-color: rgba(0, 0, 0, 0.061);
}


.infoContent::-webkit-scrollbar-thumb {  
    background-color: rgb(52, 134, 172);
    
}

.infoContent::-webkit-scrollbar-thumb:hover {  
    background-color: rgb(64, 162, 207);
}
.genderSelect {
    position: relative;
    margin: 5%;
    bottom: 0%;
    border-radius: 30%;
    padding: 3%;
    
    transition: .3s;
}

.selectedmale {
    background-color: #1d6d928e;
    border: 2px solid black;
}

.selectedfemale {
    background-color:#8e1d928a;
    border: 2px solid black;
}

.male {
    background-color: #1d6d9225;
    border: 1px solid black;
}

.female {
    background-color:#8e1d921a;
    border: 1px solid black;
}

.male:hover {
    background-color: #1d6d928e;
}

.female:hover {
    background-color:#8e1d928a;
}
</style>