<template>
<div class="inputContainer">
    <p class="LabelInputContainer">{{label}}</p>
    <input class="textInput" v-bind:class="{box_error: error.length, box_normal: !error.length}" :type="type" v-model="input">
    <div class="error_container fade-in">
      <h3 class="error_label">{{error}}</h3>
    </div>
</div>  
</template>

<script>
export default {
props: {
  label: String,
  type: String,
  error: String
},
data() {
  return {
    input: ''
  }
},
watch: {
  'input': function (val) {
    this.$emit('outputName', val)
  }
},
}
</script>

<style scope>

.error_container {
  position: absolute;
  height: auto;
  width: 50%;
  left: 25%;
  height: 5%;
  background-color: #8c2d2d00;
}

.box_error {
  border: 1px solid red;
}

.box_normal {
  border: 1px solid black;
}

.error_label {
  font-family: OpenSans-LightItalic;
  position: relative;
  margin: 0;
  padding: 0;
  margin-top: 1%;
  font-size: .8vw;
  text-align: left;
  left: 0%;
  color: red;
  animation: 1s error_label;
  opacity: 1;
}

@keyframes error_label  {
  from{opacity: 0;}
  to{opacity: 1;}
}

.inputContainer {
  position: relative;
  height: auto;
  width: 100%;
  background-color: rgba(255, 228, 196, 0);
  margin: 0;
  padding: 0;
  margin-top: 5%;
}

.LabelInputContainer {
  position: relative;
  font-size: .8vw;
  margin: 0%;
  padding: 0%;
  text-align: left;
  left: 25%;
}

.textInput {
  padding: .6vw;
  height: 100%;
  width: 45%;
  outline: 0;
  transition: .5s;
  background-color: rgba(0, 0, 0, 0.055);
  filter: drop-shadow(1px 1px 3px #000000);
  color: white;
}

.errorMsg {
  color: red;
}
</style>