<template>
  <div>
    <section>
        
    </section>
    <!--Login screen-->
    <section v-show="page === 0" class="fade-in" id="login_screen">
        <h1 class="welcome_msg">Olá</h1>
        <h2 class="welcome_mainLabel">{{primeiro_nome}}</h2>
        <inputTextBox 
            :type="'password'"
            :placeholder="'Palavra-passe'"
            :icon="'password'"
            :info="'Insere uma palavra-passe valida<br>Conter entre 6 e 36 caractéres'"
            :showCaps="true"
            @getText="data.password = $event"
            :setError="error.password"
        ></inputTextBox>
        <inputButton
            :label="'Jogar'"
            :loading="loading"
            @buttonClick="startLogin"
        ></inputButton>
        <h5 class="label_text" @click="changePage(1)">Esqueces-te da palavra passe ?</h5>
        
    </section>

    <!--Recover password step 1-->
    <section v-show="page === 1" class="fade-in">
        <h1 class="h1-label">Deseja recuperar a sua palavra passe ?</h1>
        <h5 class="h2-label-nmargin">Será enviado um codigo de recuperação para o email registado da sua conta</h5>
        <inputButton
            :label="'Recuperar palavra passe'"
            :loading="false"
            @buttonClick="requestRecoverCode"
        ></inputButton>
        <inputButton
            :label="'Voltar'"
            :loading="false"
            @buttonClick="changePage(0)"
        ></inputButton>
    </section>

    <!--Recover password step 1-->
    <section v-show="page === 2" class="fade-in">
        <h1 class="h1-label">Insira o seu codigo de recuperação !</h1>
        <inputTextBox 
            :type="'text'"
            :placeholder="'Codigo de recuperação'"
            :icon="'coding'"
            :info="'Insira o codigo de recuperação enviado por email'"
            :showCaps="false"
            @getText="verification_codeInput = $event"
            :setError="error.notSameCode"
        ></inputTextBox>
        <inputButton
            :label="'Confirmar'"
            :loading="false"
            @buttonClick="checkVerificationCode"
        ></inputButton>
    </section>

   
    <section v-show="page === 3" class="fade-in">
        <h1 class="h1-label">Insira uma nova password !</h1>
        <inputTextBox 
            :type="'password'"
            :placeholder="'Palavra-passe'"
            :icon="'password'"
            :info="'Insira a sua nova password'"
            :showCaps="true"
            @getText="data.recoverPassword = $event"
            :setError="error.password"
        ></inputTextBox>
        <inputTextBox 
            :type="'password'"
            :placeholder="'Confirma palavra-passe'"
            :icon="'password'"
            :info="'Confirme a nova password'"
            :showCaps="true"
            @getText="data.recoverNewPassword = $event"
            :setError="error.passwordSame"
        ></inputTextBox>
        <inputButton
            :label="'Mudar palavra-passe'"
            :loading="false"
            @buttonClick="changePassword"
        ></inputButton>
    </section>

    
  </div>
</template>

<script>
import inputTextBox from '../components/inputTextBox.vue'
import inputButton from '../components/inputButton.vue'
export default {
    components: {
        inputTextBox,
        inputButton
    },
    data() {
        return {
            page: 0,

            primeiro_nome: 'Cidadão',
            verification_codeInput: '',
            verification_code: '',
            data: {
                password: '',


                recoverPassword: '',
                recoverNewPassword: ''
            },

            error: {
                password: '',

                notSameCode: '',
                passwordSame: '',
            },

            loading: false,
        }
    },
    methods: {
        resetMessages: function () {
            //error
            this.error.password = '';
            this.error.notSameCode = '';
            this.error.passwordSame = '';

            //data
            this.data.password = '';
            this.data.recoverNewPassword = '';
            this.data.recoverPassword = '';
            this.verification_code = '';
            this.verification_codeInput = '';
        },

        changePage: function(page) {
            this.page = page
        },
        
        startLogin: function () {
            this.loading = true
            if(this.data.password.length >= 6 && this.data.password.length <= 36) {
                alt.emit('auth/requestLogin', this.data.password)
            } else {
                this.error.password = 'Password deverá conter entre 6 e 36 caractéres'
                this.loading = false
                return;
            }
        },

        loginResponse: function name(array) {
            //array[0] = If password is equal   |||| array[1] = verified value
            this.loading = false
            if(array[0] === null && array[1] === null) {
                this.error.password = 'Ocorreu num erro no servidor, tente mais tarde! '
                return;
            } else {
                if(array[0]) {
                    switch (array[1]) {
                        case 0: this.$emit('changepage', 2); break;
                        case 1: alt.emit('auth/beginCharCreation'); break;
                        //Starts Session and sends the permissions
                        case 2: alt.emit('auth/startSession', array[2]); break;  // <--- NOT DONE YET
                    }
                } else {
                    this.error.password = 'Password está incorreta'
                }
            }
        },

        changePassword: function () {
            const password1 = new Promise((resolve) => {
                console.log(this.data.recoverPassword + " // " + this.data.recoverPassword.length)
                if(this.data.recoverPassword.length >= 6 && this.data.recoverPassword.length <= 36)  {
                    resolve(true)
                } else {
                    resolve(false)
                    this.error.password = 'Verifica o tamanho da tua palavra-passe'
                }     
            });

            const password2 = new Promise((resolve) => {
                console.log(this.data.recoverNewPassword + " // " + this.data.recoverNewPassword.length)
                if(this.data.recoverNewPassword.length >= 5 && this.data.recoverNewPassword.length <= 36) {
                    if(this.data.recoverPassword === this.data.recoverNewPassword) {
        
                        resolve(true)
                    } else {
                        resolve(false)
                        this.error.passwordSame = 'As palavras-passes nao coincidem !'
                        this.error.password = 'As palavras-passes nao coincidem !'
                    }
                } else {
                    resolve(false)
                    this.error.password = 'Verifica o tamanho da tua palavra-passe'
                }
            });

            Promise.all([password1, password2]).then((res) =>{
                console.log(res[0] + "   /////     " +  res[1])
                if(res[0] && res[1]) {
                    alt.emit('auth:sendNewPassword', this.data.recoverPassword);  
                    
                    this.resetMessages();
                }
            })
        },
        
        generateString: function(length) {
            let characters ='ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789';
            let result = '';
            let charactersLength = characters.length;
            for ( let i = 0; i < length; i++ ) {
                result += characters.charAt(Math.floor(Math.random() * charactersLength));
            }

            return result;
        },

        requestRecoverCode: function () {

            this.verification_code = this.generateString(6)
            alt.emit('auth:requestRecoverCode', this.verification_code)
    
        },

        checkVerificationCode: function () {
            if(this.verification_code == this.verification_codeInput) {
                this.changePage(3);
                this.error.notSameCode = '';
                this.verification_code = '';
                this.verification_codeInput = '';
            } else {
                this.error.notSameCode = 'Codigo incorreto ! Verifique novamente o codigo';
            }
        },

        receivedEmailSendConfimation: function (confirmation) {
            if(confirmation) {
                this.changePage(2)  
            } else {
                //not confirm, occured an error
            }
        },

        clickedEnter: function () {
            if (this.page === 0) {
                this.startLogin();
            } else if(this.page === 2) {

            }
        },

        changedPasswordSuccesfully: function (state) {
            if(state) {
                this.error.password = '';
                this.changePage(0)
            } else {
                this.error.password = 'Ocorreu um erro a mudar a palavra-passe !'
                this.error.passwordSame = 'Ocorreu um erro a mudar a palavra-passe !'
            }
        }


    },

    mounted: function() {
        if('alt' in window) {
            alt.on('auth/authData', this.loginResponse);
            alt.on('auth/enterclicked', this.clickedEnter);
            alt.on('auth/codeVerificationConfirmation', this.receivedEmailSendConfimation);
            alt.on('auth:changedPasswordSuccesfully', this.changedPasswordSuccesfully)
        }
        
        
    },
}
</script>

<style scope>
.h1-label {
    margin: 2%;
    font-size: 1.2vw;
}

.h2-label-nmargin {
    margin: 0%;
    font-size: .8vw;
}

.label_text {
    font-family: OpenSans-Bold;
    position: relative;
    font-size: .8vw;
    left: 30%;
    text-align: left;
    margin: 4%;
    transition: .2s;
    cursor: pointer;
}

.label_text:hover {
    color: #3155df;
}

.welcome_mainLabel {
    position: absolute;
    font-family: OpenSans-Bold;
    top: 5%;
    left:40%;
    font-size: 3vw;
}

.welcome_msg {
    position: relative;
    font-family: OpenSans-SemiBold;
  
    left:-15%;
    font-size: 2vw;
}
</style>