<template>
<transition name="fade">
        <div v-show="active" class="notification_container" :class="{green: type === 0, yellow: type === 1, red: type === 2}">
    
            <div>{{notification}}</div>
    
        </div>
</transition>
</template>

<script>
export default {
props: {
    notification: String,
    type: Number,
    active: Boolean
}
}
</script>

<style scope>

.notification_container {
    position: absolute;
    font-family: opensans-semiBold;
    border: 0;
    margin: 0%;
    height: 2%;
    width: auto;
    top: 0%;
    padding: 1%;
    border-radius: 0% 0% 15px 15px;
    left: 65%;
}

.green {
    background-color: #32b85f;
}

.yellow {
    background-color: #afb832;
}

.red {
    background-color: #8c2d2d;
}
</style>