<template>
<div>
    <div class="inputContainer">
        <p class="LabelInputContainer" style="left: 44%">Altura</p>
        <input class="textInput" type="number" v-model="altura" style="width: 8%">
        <p class="LabelInputContainer" style=" position:absolute; left: 58%; top: 50%">cm</p>
        <div class="error_container fade-in">
        <h3 class="error_label">{{error}}</h3>
        </div>
    </div>  
</div>
</template>
<script>
export default {
    props: {
        height: Number,
        error: String
    },

    data() {
        return {
            altura: 170,
            unit: 'cm'
        }
    },

    watch: {
        'altura': function (val, newval) {
            if(val > 230) {
                this.altura = 230
            }
            this.$emit('bringAlt', val)
        }

    },
    
}
</script>

<style>
.alturaInput {
    margin-top: 5%;
    position: relative;
    height: auto;
    width: 100%;
}

.alturaText {
    font-size: 1.5vw;
    padding: 1%;
    width: 10%;
    background-color: transparent;
    border: 0;
    border-top: 2px solid rgba(0, 0, 0, 0.602);
    border-bottom: 2px solid rgba(0, 0, 0, 0.602);
    outline: 0;
    color:white
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

.labelAlturaTxt {
    
    position: absolute;
    font-size: 1.3vw;
    bottom: -50%;
    right: 15%;
    text-align: center;

    width: 100%;

}
</style>