<template>
  <div>
    <!--<h1 class="register_label">Registo</h1>-->

      <!--DISCORD INFORMATION -->
      <section class="discord_imgContainer">
        <div v-show="discord_loader" class="lds-facebook"><div></div><div></div><div></div></div>
        <div v-show="!discord_imgLoaded && discord_hasTimeout">
          <img class="discord_img red fade-in" src='../assets/discordLogo.png'>
        </div>
        <div v-if="discord_imgLoaded">
          <img class="discord_img green fade-in" :src='httpsURL + "cdn.discordapp.com/avatars/" + discord.id + "/" + discord.avatar'>
        </div>
      </section>
      <!--DISCORD INTEGRATION-->
      <inputTextBox 
          :type="'text'"
          :placeholder="'Discord'"
          :icon="'discord'"
          :info="'Porque a conta de discord?<hr>Receber acompanhar comunidade<br>Melhor interacção<br>'"
          :showCaps="false"
          :isDisabled="true"
          :setText="getDiscordString()"
          :setError="error.discord"
      ></inputTextBox>
      <!--REST OF THE REGISTRATION PROCESS-->
      <inputTextBox 
          :type="'text'"
          :placeholder="'Endereço de email'"
          :icon="'email'"
          :info="'Porque o email?<hr>Receber emails de recuperaçoes<br>Para ter acesso ao nosso website<br>'"
          :showCaps="true"
          @getText="register.email = $event"
          :setError="error.email"
      ></inputTextBox>
      <inputTextBox 
          :type="'password'"
          :placeholder="'Palavra-passe'"
          :icon="'password'"
          :info="'Insere uma palavra-passe valida<hr>Conter entre 6 e 36 caractéres<br><b>Recomendavel</b> palavra-passe forte'"
          :showCaps="true"
          @getText="register.password = $event"
          :setError="error.password"
      ></inputTextBox>
      <inputTextBox 
          :type="'password'"
          :placeholder="'Confirmar palavra-passe'"
          :icon="'password'"
          :showCaps="true"
          @getText="register.confirm_password = $event"
          :setError="error.confirm_password"
      ></inputTextBox>
      <inputButton
        :label="'Registar'"
        :loading="isButtonLoading"
        @buttonClick="registerRequest($event)"
      ></inputButton>
  </div>
</template>

<script>
import inputTextBox from '../components/inputTextBox.vue'
import inputButton from '../components/inputButton.vue'
import terms from '../components/auth/terms.vue'
import inputRadioButton from '../components/inputRadioButton.vue'
export default {
components: {
  inputTextBox,
  inputButton,
  inputRadioButton,
  terms
},
data() {
  return {
    httpsURL: 'https://',

    register: {
      email: '',
      password: '',
      confirm_password: '',
    },

    error: {
      discord: '',
      email: '',
      password: '',
      confirm_password: '',
    },

    discord: {
      avatar: '',
      discriminator: '',
      id: '',
      name: '',
    },
    discord_loader: true, // default true
    isButtonLoading: false, // default false
    discord_imgLoaded: false,
    discord_hasTimeout: false,
    discordTimeoutSeconds: 2,
  }
},

methods: {
  timeoutDiscord: function () {
    setTimeout(() => {
      this.discord_hasTimeout = true
      this.discord_loader = false
    }, this.discordTimeoutSeconds * 1000);
  },
  //GET STATUS OF THE DISCORD 
  getDiscordString: function () {
    if(this.discord.name.length) {
      return this.discord.name + "#" + this.discord.discriminator
    } else {
      this.error.discord = 'Nao foi detetado uma conta discord.'
      return this.error.discord
    }
  },

  //CALLBACK FROM SERVER SAYING IF THE REGISTRATION WENT WELL
  registrationCallback: function (obj) {
    this.isButtonLoading = false

  
    // [0] = email  [1] = discord
      const discordP = new Promise((resolve) => {
          if(obj[1]) {
            resolve(false)
            this.error.discord = 'Esta conta discord já esta registada !'
          } else {
            resolve(true)
          }
      })

      const emailP = new Promise((resolve) => {
          if(obj[0]) {
            resolve(false)
            this.error.email = 'O email inserido já é existente'
          } else {
            resolve(true)
          }

          
      })
      Promise.all([discordP, emailP]).then((isNotUsed) => {
          if(isNotUsed[0] && isNotUsed[1]) {
            this.$emit('changepage', 2)
          } else {
            return;
          }
      })

    
   
    
  },

  setDiscordName: function (discordData) {
    this.discord = discordData
    this.discord_imgLoaded = true
    this.discord_loader = false
  },

  registerRequest: function (e) {
    //Start the loading bar
    this.isButtonLoading = true

    //PROMISES TO SOLVE ALL INPUTS
    const email = new Promise((resolve) => {
      //email validation
      if(this.register.email.length >= 6 && this.register.email.length <= 36) {
        if(this.validateEmail(this.register.email)) {
          resolve(true)
          this.error.email = ''
        } else {
          this.error.email = 'Este email não é valido'
          resolve(false)
        }
      } else {
        this.error.email = 'Verifica o tamanho deste email'
        resolve(false)
      }
    })

    const password1 = new Promise((resolve) => {
      if(this.register.password.length >= 6 && this.register.password.length <= 36) {
        this.error.password = ''
        resolve(true)
      } else {
        this.error.password = 'A palavra passe deverá conter entre 6 a 36 caracteres'
        resolve(false)
      }
    })

    const password2 = new Promise((resolve) => {
      if(this.register.password.length >= 6 && this.register.confirm_password.length <= 36) {
        if(this.register.confirm_password === this.register.password) {
          this.error.confirm_password = ''
          //estas passwords sao iguais
          resolve(true)
        } else {
          resolve(false)
          this.error.confirm_password = 'As palavras passes não coincidem'
        }
      } else {
        resolve(false)
        this.error.confirm_password = 'A palavra passe deverá conter entre 6 a 36 caracteres'
      }
    })

    const discord = new Promise((resolve) => {
      if(this.discord.name.length) {
        resolve(true)      
      } else {
        resolve(false)
      }
    })

    const promisesResolved = Promise.all([email, password1, password2, discord]).then((result) => {
   
        if(result[0] && result[1] && result[2] && result[3]) {
          //Send request to the server
          alt.emit('auth/register/requestRegister', this.register, this.discord)
        } else {
          this.isButtonLoading = false
        }
    })
  },

  validateEmail: function(email) {
    const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  },

},

mounted() {
  if('alt' in window) {
    this.timeoutDiscord()
    alt.on('auth/register/emailExistance', this.registrationCallback)
    alt.on('auth/discord', this.setDiscordName)
  }
}
}
</script>

<style>
.red {
  border: 2px solid red
}

.green {
  border: 2px solid #32b85f;
}

.register_label {
  position: absolute;
  font-family: OpenSans-BoldItalic;
  font-size: 2.5vw;
  width: 100%;
  height: 100%;
  top: 0%;
  left: 0%;
  text-align: center;
}

.discord_img {
  border-radius: 50%;
  width: 15%;
  
}


.lds-facebook {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-facebook div {
  display: inline-block;
  position: absolute;
  left: 8px;
  width: 16px;
  background: #fff;
  animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.lds-facebook div:nth-child(1) {
  left: 8px;
  animation-delay: -0.24s;
}
.lds-facebook div:nth-child(2) {
  left: 32px;
  animation-delay: -0.12s;
}
.lds-facebook div:nth-child(3) {
  left: 56px;
  animation-delay: 0;
}
@keyframes lds-facebook {
  0% {
    top: 8px;
    height: 64px;
  }
  50%, 100% {
    top: 24px;
    height: 32px;
  }
}
</style>