<template>
  <div class="buttonContainer" @click="emitClick">
    <img class="letter_left" :src="getLetterimg(index)">
    <div class="answer_text fade-in">
        {{answer}}
    </div>
  </div>
</template>

<script>
export default {
    props: {
        index: Number,
        answer: String,
        isSelected: Boolean,
    },
    data() {
        return {
            manuallyisSelected: false
      
        }
    },
    methods: {
        emitClick: function (index) {
      
            this.$emit('emitClick', index)
        },
        getLetterimg: function (index) {
            this.index = index
            switch (index) {
                case 0: return require("../../assets/icons/quiz/letter-a.svg"); break;
                case 1: return require("../../assets/icons/quiz/letter-b.svg"); break;
                case 2: return require("../../assets/icons/quiz/letter-c.svg"); break;
                case 3: return require("../../assets/icons/quiz/letter-d.svg"); break;
            }
        },
    },
}
</script>

<style scope>
.buttonContainer {
    position: relative;
    width: 100%;
    height: 20%;
    top: 0%;
    margin: 1.5%;
    background: linear-gradient(90deg, #2e3136 0%, #32363f 50%, #32363f 100%);
    z-index: 1;
    border-radius: 50px;
    cursor: pointer;
}

.letter_left {
    position: absolute;
    width: 12%;
    height: 100%;
    margin-left: 5%;
    left: -6%;
    transition: .2s;
    z-index: 2s;
    cursor: pointer;
    filter: saturate(1);
}



div.answer_text {
  position: relative;
  text-size-adjust: 1.5vw;
  text-align: left;
  width:80%;
  margin-left:10%;
  padding: 1%;
  word-wrap: break-word;
}



</style>